            <b-button pill size="sm" variant="success" to="/userinfo" v-if="this.getAuth===true">사용자설정</b-button>
            <b-button size="sm" variant="danger" @click="this.signOut">로그아웃</b-button>                        
            <!-- <b-nav-item to="/ticketcount" v-if="this.getAuth>=1">당일식수</b-nav-item>
            <b-button @click="this.signOut" size="sm" variant="danger">로그아웃</b-button> -->
            
        </b-nav>        
    </div>    
</template>

<script>
export default {
    computed: {
        // getPhoto() {
        // return this.$store.state.auth.user.photoURL;
        // },
        getAuth() {
            return this.$store.state.auth.user.authority;
        },
        getUserName() {
